import { MDXProvider } from "solid-mdx";
import { MDXComponents } from "./components/mdx-component";
import { HashRouter as Router } from "@solidjs/router";
import { Layout, ThemeProvider } from "guya-ui";
import "./index.css";
import Navbar from "./layout/navbar";
import { Show, Suspense } from "solid-js";
import { Sider } from "./layout/sider";
import { routes } from "./routes";
import { urlPrefix } from "./contants";
import { Anchor } from "./layout/anchor";
import { createStore } from "solid-js/store";

export const seedTokenStore = createStore({
  colorPrimary: "#1890ff",
});
const [seedToken] = seedTokenStore;

export default function App() {
  return (
    <>
      <MDXProvider components={MDXComponents}>
        <Router
          root={(props) => {
            return (
              <ThemeProvider
                value={{
                  seedToken,
                }}
              >
                <Layout>
                  <Layout.Header
                    style={{
                      background: "#fff",
                      position: "sticky",
                      "z-index": 99,
                      top: "0",
                    }}
                  >
                    <Navbar />
                  </Layout.Header>
                  <Show
                    when={props.location.pathname === "/" + urlPrefix}
                    fallback={
                      <div
                        style={{
                          "padding-left": "250px",
                          "padding-top": "64px",
                        }}
                      >
                        <Layout.Sider
                          style={{
                            position: "fixed",
                            left: 0,
                            top: "64px",
                            width: "250px",
                          }}
                        >
                          <Sider />
                        </Layout.Sider>
                        <Layout.Content
                          style={{
                            padding: "48px 32px",
                            display: "flex",
                          }}
                        >
                          <div style={{ width: "100%" }}>
                            <Suspense>{props.children}</Suspense>
                          </div>
                          <aside class="anchor-side" style={{ width: "300px" }}>
                            <Anchor />
                          </aside>
                        </Layout.Content>
                      </div>
                    }
                  >
                    <Suspense>{props.children}</Suspense>
                  </Show>
                </Layout>
              </ThemeProvider>
            );
          }}
        >
          {routes}
        </Router>
      </MDXProvider>
    </>
  );
}
